@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
body {
  font-family: 'muli';
  box-sizing: border-box;
  display: grid;
  overflow: hidden;
}

#root{
  height: 99vh;
  width: 99vw;
  display: grid;
  overflow: hidden;
}

.container{
    display: grid;
}
.calendar{
    grid-row: 1/2;
    grid-column: 1/2;
    background-color: #d2dae2;
    display: grid;
    grid-template-columns: 24% 76%;
    grid-template-rows: 150px 20px auto;
}
.headerdiv{
    display: grid;
    grid-row: 1/2;
    grid-column: 1/3;
}
.headerwrap{
    display: grid;
    grid-auto-flow: column;
    height: 150px;
    align-items: center;
    background-color: #1e272e;
    margin: 0 1px 0 1px;
}
.demoalert{
    grid-row: 1/2;
    grid-column: 1/2;
    align-self: start;
    margin: 20px 0 0 10px;
}
.demoalert span{
    border: 2px solid #d2dae2;
    color: #d2dae2;
    padding: 8px;
}
.header-col{
    grid-row: 1/2;
    grid-column: 1/2;
    justify-self: center;
    font-family: 'oswald';
    font-size: 24px;
    font-weight: 200;
