    margin-left: 18px;
    z-index: 101;
}
.subtitle{
    grid-row: 1/2;
    grid-column: 1/2;
    color: #1e272e;
    font-family: 'oswald';
    font-size: 48px;
    font-weight: 200;
    margin-left: 16px;
    margin-top: 164px;
}
.month{
    font-size: 64px;
}
.daysdiv{
    display: grid;
    grid-row: 2/3;
    grid-column: 1/3;
}
.dayswrap{
    display: grid;
    grid-auto-flow: column;
    background: #1e272e;
    color: #d2dae2;
