    height: 20px;
    width: 76%;
    justify-self: end;
}
.day-col{
    justify-self: center;
    font-family: 'muli';
    -webkit-transform: translatey(-8px);
            transform: translatey(-8px);
}
.modaldiv{
    grid-row: 3/4;
    grid-column: 1/2;
    color: #1e272e;
    font-size: 14px;
    font-family: 'muli';
