    padding: 10px;
    height: 794px;
    z-index: 2;
}
.cellsdiv {
    display: grid;
    grid-row: 3/4;
    grid-column: 2/3;
}
.monthtoggle,
.monthtoggle.flip{
    grid-row: 1/4;
    grid-column: 1/3;
    align-self: end;
    justify-self: end;
    position: relative;
    margin-bottom: 18px;
    margin-right: 1px;
    display: grid;
    background-color: #d2dae2;
    border: 1px solid #1e272e;
    width: 203px;
    height: 143px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: 0.5s ease;
    transition: 0.5s ease;
    z-index: 2;
}
.monthtoggle.flip{
    -webkit-transform: rotatey(180deg);
            transform: rotatey(180deg);
}
.monthtoggle:active,
.monthtoggle.flip:active,
.wifitoggle:active,
.wifitoggle.flip:active,
.maptoggle:active,
.maptoggle.flip:active,
