    margin-right: 208px;
    display: grid;
    background-color: #d2dae2;
    border: 1px solid #1e272e;
    width: 202px;
    height: 143px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: 0.5s ease;
    transition: 0.5s ease;
    z-index: 2;
}
.wifitoggle.flip{
    -webkit-transform: rotatey(180deg);
            transform: rotatey(180deg);
}
.maptoggle,
.maptoggle.flip{
    grid-row: 1/4;
    grid-column: 1/3;
    align-self: end;
    justify-self: end;
    position: relative;
    margin-bottom: 18px;
    margin-right: 414px;
    display: grid;
    background-color: #d2dae2;
    border: 1px solid #1e272e;
    width: 202px;
    height: 143px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: 0.5s ease;
    transition: 0.5s ease;
    z-index: 2;
}
.maptoggle.flip{
    -webkit-transform: rotatey(180deg);
            transform: rotatey(180deg);
}
.testbutton{
    grid-row: 1/2;
    grid-column: 1/3;
    align-self: center;
    justify-self: end;
    margin-right: 34px;
    display: grid;
    align-items: center;
    justify-content: center;
    background-color: #d2dae2;
    border: 1px solid #1e272e;
    width: 34px;
    height: 34px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: 0.5s ease;
    transition: 0.5s ease;
    z-index: 2;
}
.calendarkey{
    grid-row: 1/4;
    grid-column: 1/3;
    align-self: end;
    justify-self: end;
    position: relative;
    margin-bottom: 18px;
    margin-right: 622px;
    display: grid;
    grid-template-columns: 50% 50%;
    background-color: #d2dae2;
    width: 202px;
    height: 143px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: 0.5s ease;
    transition: 0.5s ease;
    z-index: 2;
}
.leftkey{
    grid-column: 1/2;
    display: grid;
}
.rightkey{
    grid-column: 2/3;
    display: grid;
}
#keytitle{
    font-family: 'oswald';
    font-size: 20px;
    font-weight: 200;
}
.calendarkey span{
