    display: grid;
    align-items: center;
    justify-content: center;
}
.calendarkey #ClosureTypeOne,
.calendarkey #ClosureTypeTwo{
    color: #d2dae2;
    grid-column: 1/2;
}
.calendarkey #ClosureTypeThree{
    color: #1e272e;
    grid-column: 1/2;
}
#keyHoliday{
    background-color: white;
    color: #1e272e;
    grid-column: 1/2;
}
.togglefront{
    grid-row: 1/2;
    grid-column: 1/2;
    width: 202px;
    display: grid;
    grid-template-rows: 75% 25%;
    background-color: none;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    text-align: center;
    pointer-events: none;
}
.toggleback{
    grid-row: 1/2;
    grid-column: 1/2;
    width: 202px;
    display: grid;
    grid-template-rows: 75% 25%;
    background-color: none;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    text-align: center;
    -webkit-transform: rotatey(-180deg);
            transform: rotatey(-180deg);
    pointer-events: none;
}
.monthtoggle .togglefront,
.monthtoggle .toggleback{
    width: 203px;
}
.togglemain{
    justify-self: center;
    align-self: center;
    font-family: 'muli';
    color: #1e272e;
    font-size: 16px;
    font-weight: 600;
    grid-column: 1/2;
    grid-row: 1/2;
    width: 200px;
}
.togglefront img,
.toggleback img{
    width: 50px;
}
.togglefront span,
.toggleback span{
    grid-column: 1/2;
    grid-row: 2/3;
    background: #1e272e;
    display: grid;
    align-items: center;
    justify-content: center;
    color: #d2dae2;
    font-size: 14px;
}
.row{
    justify-self: end;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 147px;
    background-color: white;
}
.column{
    flex-grow: 1;
    flex-basis: 0;
    max-width: 100%;
}
.cell{
    color: #1e272e;
    border: 1px solid #1e272e;
    margin: 1px;
    font-family: 'muli';
}
.disabled,
.dis-selected{
    color: #808e9b;
}
.calendar .cellsdiv .cell {
    position: relative;
    overflow: hidden;
    cursor: pointer;
    background: #d2dae2;
    -webkit-transition: 0.25s ease-out;
    transition: 0.25s ease-out;
    display: grid;
}
.calendar .cellsdiv .selected,
.calendar .cellsdiv .cell:active {
    background-color: #1e272e;
    flex-grow: 2.79;
    -webkit-transition: 0.5s ease-out;
    transition: 0.5s ease-out;
}
.calendar .cellsdiv .dis-selected,
.calendar .cellsdiv .dis-selected:active,
.calendar .cellsdiv .disabled:active {
    background-color: #808e9b;
    flex-grow: 2.79;
    -webkit-transition: 0.5s ease-out;
    transition: 0.5s ease-out;
}
.calendar .cellsdiv .current{
    background-color: #1e272e;
    -webkit-transition: 0.5s ease-out;
    transition: 0.5s ease-out;
}
.calendar .cellsdiv .cell:active .number,
.calendar .cellsdiv .selected .number,
.calendar .cellsdiv .dis-selected .number,
.calendar .cellsdiv .current .number{
    visibility: hidden;
}
.calendar .cellsdiv .cell .number{
    justify-self: end;
    grid-row: 1/2;
    grid-column: 1/2;
    margin-right: 4px;
    font-weight: bold;
    font-size: 14px;
}
.calendar .cellsdiv .cell .bg {
    font-weight: 700;
    line-height: 1;
    color: #d2dae2;
    opacity: 0;
    font-size: 48px;
    -webkit-transition: 0.25s ease-out;
    transition: 0.25s ease-out;
    margin-right: 4px;
    justify-self: end;
    grid-row: 1/2;
    grid-column: 1/2;
}
.calendar .cellsdiv .cell:active .bg,
.calendar .cellsdiv .selected .bg,
.calendar .cellsdiv .current .bg  {
    margin-right: 10px;
    margin-top: 4px;
    opacity: 1;
    -webkit-transition: 0.5s ease-in;
    transition: 0.5s ease-in;
    -webkit-animation: neon .08s ease-in-out infinite alternate;
            animation: neon .08s ease-in-out infinite alternate;
}
.calendar .cellsdiv .disabled:active .bg,
.calendar .cellsdiv .dis-selected .bg,
.calendar .cellsdiv .dis-selected:active .bg {
    margin-right: 10px;
    margin-top: 4px;
    opacity: 1;
    -webkit-transition: 0.5s ease-in;
    transition: 0.5s ease-in;
    -webkit-animation: neon-dis .08s ease-in-out infinite alternate;
            animation: neon-dis .08s ease-in-out infinite alternate;
}
@-webkit-keyframes neon {
  from {
    text-shadow:
    0 0 6px rgba(202,228,225,0.92),
    0 0 30px rgba(202,228,225,0.34),
    0 0 12px rgba(239, 87, 119,0.52),
    0 0 21px rgba(239, 87, 119,0.92),
    0 0 34px rgba(239, 87, 119,0.78),
    0 0 54px rgba(239, 87, 119,0.92);
  }
  to {
    text-shadow:
    0 0 6px rgba(202,228,225,0.98),
    0 0 30px rgba(202,228,225,0.42),
    0 0 12px rgba(239, 87, 119,0.58),
    0 0 22px rgba(239, 87, 119,0.84),
    0 0 38px rgba(239, 87, 119,0.88),
    0 0 60px rgba(239, 87, 119,1);
  }
}
@keyframes neon {
  from {
    text-shadow:
    0 0 6px rgba(202,228,225,0.92),
    0 0 30px rgba(202,228,225,0.34),
    0 0 12px rgba(239, 87, 119,0.52),
    0 0 21px rgba(239, 87, 119,0.92),
    0 0 34px rgba(239, 87, 119,0.78),
    0 0 54px rgba(239, 87, 119,0.92);
  }
  to {
    text-shadow:
    0 0 6px rgba(202,228,225,0.98),
    0 0 30px rgba(202,228,225,0.42),
    0 0 12px rgba(239, 87, 119,0.58),
    0 0 22px rgba(239, 87, 119,0.84),
    0 0 38px rgba(239, 87, 119,0.88),
    0 0 60px rgba(239, 87, 119,1);
  }
}
@-webkit-keyframes neon-dis {
  from {
    text-shadow:
    0 0 6px rgba(202,228,225,0.92),
    0 0 30px rgba(202,228,225,0.34),
    0 0 12px rgba(30, 39, 46,0.52),
    0 0 21px rgba(30, 39, 46,0.92),
    0 0 34px rgba(30, 39, 46,0.78),
    0 0 54px rgba(30, 39, 46,0.92);
  }
  to {
    text-shadow:
    0 0 6px rgba(202,228,225,0.98),
    0 0 30px rgba(202,228,225,0.42),
    0 0 12px rgba(30, 39, 46,0.58),
    0 0 22px rgba(30, 39, 46,0.84),
